import { Component, Input, OnInit, Inject } from '@angular/core';
import { ClassService } from "@core/services/class.service";
import { DOCUMENT } from '@angular/common';
@Component({
  selector: 'app-student-lists',
  templateUrl: './student-lists.component.html',
  styleUrls: ['./student-lists.component.css']
})
export class StudentListsComponent implements OnInit {
  @Input() studentLists: any = [];
  @Input() classId: any;

  public selectedUser: any = {};
  public feedbackUserId: any = {};
  public isFeedback: boolean = false;
  public feedbackValue: String;
  public selectedIndex:String;
  public feebBackLists:any=[];

  constructor(private _ser: ClassService, @Inject(DOCUMENT) private _document: Document) { }

  ngOnInit(): void {
  }

  showAttandance(stud: any) {
    this.selectedUser = this.selectedUser.userId == stud.userId ? {} : stud;
  }

  onAttandance(condition: boolean) {
    let temp = {
      "attendance": condition
    }
    this._ser.onAttandance(temp, this.selectedUser.userId, this.classId)
      .subscribe(() => {
        this.selectedUser = {};
        this._document.defaultView.location.reload();
      })
  }

  showFeedBack(stud: any,selectedIndex) {
    this.selectedUser = {};
    this.feedbackUserId = stud.userId;
    this.isFeedback = true;
    this.selectedIndex=selectedIndex;
    this.getClassFeedBack();
  }

  getValue(val: String) {
    this.feedbackValue = val;
  }

  getClassFeedBack() {
    this._ser.getClassFeedback(this.classId)
      .subscribe((res) => {
        this.feebBackLists=res.feedbacks;
      })
  }


  createFBack() {
    this._ser.createFeedback(this.classId,this.feedbackUserId,this.feedbackUserId,this.feedbackValue)
    .subscribe((res)=>{
      console.error(res);
    })
  }
}
