import { Component, AfterViewInit,OnDestroy, ElementRef, ChangeDetectorRef, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { LoaderService } from "@core/services/loader.service";
import { LoaderState } from '@app/shared/models/loader';

@Component({
  selector: 'app-disable-call-loading',
  templateUrl: './disable-call-loading.component.html',
  styleUrls: ['./disable-call-loading.component.css']
})
export class DisableCallLoadingComponent implements OnInit,OnDestroy {
  public isShow:boolean=true;
  loadingSubscription: Subscription;
  
  constructor(
    private loadingScreenService: LoaderService,
    private _elmRef: ElementRef,
    private _changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    // this.loadingSubscription = this.loadingScreenService.loading$.pipe().subscribe(
    //   (status: boolean) => {
    //     this.isShow=status;
    //     this._elmRef.nativeElement.style.display = status ? 'none' : 'block';
    //     this._changeDetectorRef.detectChanges();
    //   }
    // );

    this.loadingSubscription = this.loadingScreenService.loaderState
    .subscribe((state: LoaderState) => {
      this.isShow = state.show;
    });
  }

  ngOnDestroy() {
    this.loadingSubscription.unsubscribe();
  }
}
