import { Component, Input, OnInit } from '@angular/core';
import { Days } from '@app/enum/app_enum';
import { ClassService } from "@core/services/class.service";
@Component({
  selector: 'app-timetable',
  templateUrl: './timetable.component.html',
  styleUrls: ['./timetable.component.css']
})
export class TimetableComponent implements OnInit {
  @Input() classId:any;
  timetableData: any = {};
  public days: any = [Days.Mon, Days.Tue, Days.Wed, Days.Thu, Days.Fri, Days.Sat, Days.Sun];

  constructor(private _ser: ClassService) { }

  ngOnInit(): void {

    this.getTimeLine();
  }

  getTimeLine() {
    this._ser.getTimeLine(this.classId).subscribe((res) => {
      this.timetableData = res;
    })
  }

}
